import { useEffect } from "react"

type UseInitialScrollPageProps = {
  page: HTMLCanvasElement | null
  pageNumber: number
  initPage: number
}

const useInitialScrollPage = ({ page, pageNumber, initPage }: UseInitialScrollPageProps): void => {
  useEffect(() => {
    if (page && pageNumber === initPage) {
      page.scrollIntoView({ behavior: "smooth" })
    }
  }, [page, pageNumber, initPage])
}

export { useInitialScrollPage }
